@import "variables";

body{
  #delivery {
    margin: 0 auto;
    padding: 25px 0;
    width: 735px;
    min-height: 250px;
    position: relative;
    border-radius: 5px;
    background: #fff;
    .icon-cancel {
      font-size: 1.375em;
      text-align: right;
      color: @mainColor;
    }
    #deliveryClose {
      width: 21px;
      height: 21px;
      position: absolute;
      top: 6px;
      right: 10px;
      cursor: pointer;
      display: block;
    }
    h5 {
      margin: 0 0 0 20px;
      font: 1.125em/1.25em @sansbold;
      color: @mainColor;
    }
    p {
      margin: 0 25px;
      font: .875em/1.375em @sansregular;
      color: #474747;
    }
    ul{
      margin: 0 25px;
      list-style: disc inside;
      li{
        font: .875em/1.375em @sansregular;
        color: #474747;
      }
    }
  }
}